.app-shell {

  min-height: 100vh;

  padding: 48px 24px;

  box-sizing: border-box;

  background: radial-gradient(circle at 15% 20%, rgba(108, 92, 231, 0.38) 0%, rgba(15, 24, 43, 0.95) 48%, rgba(9, 12, 24, 1) 100%);

  display: flex;

  justify-content: center;

  align-items: center;

}



.app-content {

  --transition-ms: 800ms;

  --transition-ease: cubic-bezier(0.4, 0, 0.2, 1);

  --transition-fade-ease: cubic-bezier(0.4, 0, 0.2, 1);

  width: min(1100px, 100%);

  background: rgba(10, 16, 28, 0.72);

  backdrop-filter: blur(22px);

  border-radius: 28px;

  padding: 40px 44px;

  box-shadow: 0 32px 60px rgba(0, 0, 0, 0.35);

  color: #f7f9ff;

  display: flex;

  flex-direction: column;

  gap: 32px;

}



.app-footer {

  margin-top: auto;

  padding-top: 16px;

  border-top: 1px solid rgba(247, 249, 255, 0.12);

  font-size: 0.85rem;

  color: rgba(247, 249, 255, 0.6);

  text-align: center;

}



.app-header h1 {

  margin: 0;

  font-size: clamp(1.8rem, 4vw, 2.6rem);

  font-weight: 700;

  letter-spacing: 0.02em;

}



.app-header p {

  margin: 12px 0 0;

  color: rgba(247, 249, 255, 0.75);

  font-size: 1rem;

}



.app-status {

  padding: 18px 24px;

  background: rgba(255, 255, 255, 0.06);

  border-radius: 16px;

  font-weight: 500;

}



.app-status.error {

  background: rgba(244, 67, 54, 0.12);

  color: #ff9c8e;

}



.controls-panel {

  display: flex;

  flex-direction: column;

  gap: 20px;

}



.controls-panel-top {

  margin-bottom: 20px;

}



.controls-panel-bottom {

  margin-top: 24px;

}





.controls-row {

  display: flex;

  flex-wrap: wrap;

  gap: 12px;

  align-items: center;

}



.controls-grid {

  display: grid;

  grid-template-columns: repeat(auto-fit, minmax(160px, 1fr));

  gap: 16px;

}



.control-field {

  display: flex;

  flex-direction: column;

  gap: 6px;

}



.control-select {

  appearance: none;

  background: rgba(255, 255, 255, 0.08);

  border: none;

  border-radius: 12px;

  padding: 12px 16px;

  color: #f7f9ff;

  font-size: 0.95rem;

  font-weight: 500;

  box-shadow: 0 8px 18px rgba(0, 0, 0, 0.18);

  transition: box-shadow 200ms ease, transform 200ms ease;

}



.control-select:focus {

  outline: 2px solid rgba(84, 160, 255, 0.6);

  outline-offset: 2px;

  box-shadow: 0 12px 28px rgba(0, 0, 0, 0.24);

  transform: translateY(-1px);

}



.control-select:disabled {

  opacity: 0.5;

  cursor: not-allowed;

  box-shadow: none;

}



.control-select option {

  color: #0b1322;

  background: #f7f9ff;

  font-weight: 500;

}





.control-btn {

  padding: 12px 22px;

  border-radius: 999px;

  border: none;

  font-size: 0.95rem;

  font-weight: 600;

  letter-spacing: 0.01em;

  cursor: pointer;

  background: rgba(255, 255, 255, 0.08);

  color: #f7f9ff;

  transition: transform 220ms ease, background 220ms ease, box-shadow 220ms ease;

  box-shadow: 0 10px 24px rgba(0, 0, 0, 0.18);

}



.control-btn:hover:not(:disabled) {

  transform: translateY(-2px);

  background: rgba(255, 255, 255, 0.16);

}



.control-btn:disabled {

  cursor: not-allowed;

  opacity: 0.45;

  box-shadow: none;

}



.control-btn.primary {

  background: linear-gradient(135deg, #6c5ce7, #12cbc4);

  color: #0b1322;

}



.control-btn.primary:hover:not(:disabled) {

  background: linear-gradient(135deg, #a29bfe, #55efc4);

}



.control-btn.speed {

  background: rgba(255, 255, 255, 0.06);

  font-variant-numeric: tabular-nums;

}



.control-btn.speed.active {

  background: linear-gradient(135deg, #ff9ff3, #54a0ff);

  color: #081020;

}



.control-label {

  display: block;

  font-size: 0.9rem;

  color: rgba(247, 249, 255, 0.72);

  font-weight: 600;

  letter-spacing: 0.01em;

}



.speed-toggle {

  display: flex;

  flex-wrap: wrap;

  gap: 8px;

}



.timeline {

  display: flex;

  flex-direction: column;

  gap: 8px;

}



.timeline-bar {

  width: 100%;

  height: 8px;

  background: rgba(255, 255, 255, 0.12);

  border-radius: 999px;

  overflow: hidden;

}



.timeline-progress {

  height: 100%;

  background: linear-gradient(90deg, #6c5ce7, #f368e0);

  border-radius: inherit;

  transition: width var(--transition-ms, 800ms) var(--transition-width-ease, cubic-bezier(0.33, 0, 0.2, 1));

}



.timeline-meta {

  display: flex;

  justify-content: space-between;

  font-size: 0.85rem;

  color: rgba(247, 249, 255, 0.6);

}



@media (max-width: 768px) {

  .app-shell {

    padding: 32px 16px;

  }



  .app-content {

  --transition-ms: 800ms;

  --transition-ease: cubic-bezier(0.4, 0, 0.2, 1);

  --transition-fade-ease: cubic-bezier(0.4, 0, 0.2, 1);

  width: min(1100px, 100%);

  background: rgba(10, 16, 28, 0.72);

  backdrop-filter: blur(22px);

  border-radius: 28px;

  padding: 40px 44px;

  box-shadow: 0 32px 60px rgba(0, 0, 0, 0.35);

  color: #f7f9ff;

  display: flex;

  flex-direction: column;

  gap: 32px;

}



  .controls-row {

    flex-direction: column;

    align-items: flex-start;

  }



  .control-btn {

    width: 100%;

    justify-content: center;

  }

}

.race-container {

  display: flex;

  flex-direction: column;

  gap: 20px;

  width: 100%;

}



.race-date {

  font-size: clamp(1.6rem, 3vw, 2.3rem);

  font-weight: 700;

  letter-spacing: 0.04em;

  color: #ffffff;

  text-shadow: 0 10px 30px rgba(0, 0, 0, 0.4);

}



.race-chart {

  position: relative;

  width: 100%;

  border-radius: 20px;

  background: rgba(255, 255, 255, 0.04);

  padding: 10px 0;

  overflow: hidden;

  contain: layout paint;

}



.bar-row {

  position: absolute;

  left: 0;

  right: 0;

  height: 32px;

  display: grid;

  grid-template-columns: 52px minmax(120px, 180px) 1fr 120px;

  align-items: center;

  gap: 16px;

  padding: 0 24px;

  color: #f7f9ff;

  transition:

    transform var(--transition-ms, 800ms) var(--transition-ease, cubic-bezier(0.4, 0, 0.2, 1)),

    opacity calc(var(--transition-ms, 800ms) * 0.9) var(--transition-fade-ease, cubic-bezier(0.4, 0, 0.2, 1));

  will-change: transform, opacity;

  z-index: 2;

}



.bar-row.entering {

  animation: fade-in calc(var(--transition-ms, 800ms) * 0.85)

    var(--transition-fade-ease, cubic-bezier(0.4, 0, 0.2, 1)) forwards;

  opacity: 0;

}



.bar-row.ghost {

  pointer-events: none;

  opacity: 1;

  z-index: 1;

  transition: opacity calc(var(--transition-ms, 800ms) * 0.8)

    var(--transition-fade-ease, cubic-bezier(0.4, 0, 0.2, 1));

}



.bar-row.ghost.fading {

  opacity: 0;

}



.bar-rank {

  font-size: 1.05rem;

  font-weight: 700;

  color: rgba(255, 255, 255, 0.78);

}



.bar-label {

  font-weight: 600;

  color: rgba(255, 255, 255, 0.94);

  overflow: hidden;

  text-overflow: ellipsis;

  white-space: nowrap;

}



.bar-track {

  position: relative;

  width: 100%;

  height: 100%;

  background: rgba(255, 255, 255, 0.08);

  border-radius: 999px;

  overflow: hidden;

}



.bar-fill {

  height: 100%;

  border-radius: inherit;

  will-change: width, background;

  transition: width var(--transition-ms, 800ms) var(--transition-width-ease, cubic-bezier(0.33, 0, 0.2, 1)),

    background calc(var(--transition-ms, 800ms) * 0.6) ease;

}



.bar-value {

  font-variant-numeric: tabular-nums;

  font-weight: 600;

  color: rgba(255, 255, 255, 0.86);

  justify-self: end;

}



@keyframes fade-in {

  from {

    opacity: 0;

  }

  to {

    opacity: 1;

  }

}



@media (max-width: 768px) {

  .race-chart {

    padding: 16px 0 24px;

  }



  .bar-value {

    font-size: 0.85rem;

  }

}































.sr-only {

  position: absolute;

  width: 1px;

  height: 1px;

  padding: 0;

  margin: -1px;

  overflow: hidden;

  clip: rect(0, 0, 0, 0);

  white-space: nowrap;

  border: 0;

}



.trend-range {

  display: flex;

  flex-direction: column;

  gap: 14px;

  margin-top: 22px;

}



.trend-range-slider {

  display: flex;

  align-items: center;

  gap: 14px;

}



.trend-range-value {

  min-width: 52px;

  text-align: center;

  font-variant-numeric: tabular-nums;

  color: rgba(247, 249, 255, 0.86);

  font-weight: 600;

}



.trend-range-slider-control {

  position: relative;

  flex: 1;

  height: 12px;

  border-radius: 999px;

  background: linear-gradient(

    90deg,

    rgba(255, 255, 255, 0.18) var(--range-start, 0%),

    rgba(84, 160, 255, 0.75) var(--range-start, 0%),

    rgba(84, 160, 255, 0.75) var(--range-end, 100%),

    rgba(255, 255, 255, 0.18) var(--range-end, 100%)

  );

  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.06);

}



.trend-range-input {

  position: absolute;

  top: -6px;

  left: 0;

  width: 100%;

  margin: 0;

  pointer-events: none;

  background: none;

  -webkit-appearance: none;

  appearance: none;

  height: 24px;

}



.trend-range-input::-webkit-slider-thumb {

  pointer-events: auto;

  -webkit-appearance: none;

  height: 18px;

  width: 18px;

  border-radius: 50%;

  background: linear-gradient(135deg, rgba(84, 160, 255, 0.85), rgba(140, 122, 230, 0.85));

  border: 2px solid rgba(12, 18, 30, 0.75);

  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.35);

}



.trend-range-input::-moz-range-thumb {

  pointer-events: auto;

  height: 18px;

  width: 18px;

  border-radius: 50%;

  background: linear-gradient(135deg, rgba(84, 160, 255, 0.85), rgba(140, 122, 230, 0.85));

  border: 2px solid rgba(12, 18, 30, 0.75);

  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.35);

}



.trend-range-input::-ms-thumb {

  pointer-events: auto;

  height: 18px;

  width: 18px;

  border-radius: 50%;

  background: linear-gradient(135deg, rgba(84, 160, 255, 0.85), rgba(140, 122, 230, 0.85));

  border: 2px solid rgba(12, 18, 30, 0.75);

  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.35);

}



.trend-range-input:focus-visible::-webkit-slider-thumb {

  outline: 2px solid rgba(255, 255, 255, 0.9);

  outline-offset: 2px;

}



.trend-range-input::-webkit-slider-runnable-track,

.trend-range-input::-moz-range-track,

.trend-range-input::-ms-track {

  height: 24px;

  background: transparent;

  border-color: transparent;

  color: transparent;

}



@media (max-width: 720px) {

  .trend-range-slider {

    flex-direction: column;

    align-items: stretch;

  }



  .trend-range-value {

    text-align: left;

  }

}



.trend-combobox {
  position: relative;
  margin: 12px 0 28px;
}




.trend-combobox-input {

  padding-right: 42px;

}



.trend-combobox::after {

  content: '';

  position: absolute;

  top: 50%;

  right: 12px;

  transform: translateY(-50%);

  width: 0;

  height: 0;

  border-left: 6px solid transparent;

  border-right: 6px solid transparent;

  border-top: 6px solid rgba(247, 249, 255, 0.7);

  pointer-events: none;

}







.trend-combobox-menu {

  position: absolute;

  top: calc(100% + 6px);

  left: 0;

  width: 100%;

  max-height: 260px;

  overflow-y: auto;

  background: rgba(9, 15, 27, 0.95);

  border-radius: 14px;

  box-shadow: 0 22px 40px rgba(0, 0, 0, 0.4);

  padding: 8px 0;

  z-index: 40;

}





.trend-combobox-option {

  width: 100%;

  text-align: left;

  padding: 10px 18px;

  background: transparent;

  border: none;

  color: rgba(247, 249, 255, 0.9);

  font-size: 0.95rem;

  font-weight: 500;

  cursor: pointer;

  transition: background 160ms ease, color 160ms ease;

}



.trend-combobox-option:hover,
.trend-combobox-option.active {
  background: rgba(84, 160, 255, 0.2);
  color: #ffffff;
}

.trend-selected-name {
  margin: 26px 0 10px;
  text-align: center;
}

.trend-selected-name h3 {
  margin: 0;
  font-size: 1.7rem;
  letter-spacing: 0.04em;
}

.trend-chart-subtitle {
  margin: 18px 0;
  color: rgba(247, 249, 255, 0.65);
  font-size: 0.95rem;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  text-align: left;
}

.trend-name-meaning {
  margin: 18px 0 14px;
  padding: 18px 22px 20px;
  border-radius: 20px;
  background: linear-gradient(135deg, rgba(84, 160, 255, 0.18), rgba(140, 122, 230, 0.18));
  border: 1px solid rgba(255, 255, 255, 0.12);
  color: rgba(247, 249, 255, 0.9);
  box-shadow: 0 16px 30px rgba(0, 0, 0, 0.28);
  position: relative;
}

.trend-name-meaning::before {
  content: '';
  position: absolute;
  inset: 12px;
  border-radius: 16px;
  border: 1px solid rgba(255, 255, 255, 0.08);
  pointer-events: none;
}

.trend-name-meaning-badge {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 6px 12px;
  border-radius: 999px;
  background: rgba(9, 15, 27, 0.65);
  color: rgba(247, 249, 255, 0.82);
  font-size: 0.75rem;
  font-weight: 700;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  position: relative;
  z-index: 1;
}

.trend-name-meaning-badge::before {
  content: 'âœ¶';
  font-size: 0.8rem;
  color: rgba(251, 197, 49, 0.85);
}

.trend-name-meaning-text {
  margin: 14px 0 0;
  font-size: 1rem;
  line-height: 1.65;
  position: relative;
  z-index: 1;
}

.trend-name-meaning-text em {
  font-style: italic;
  color: #ffffff;
}

.trend-combobox-empty {
  position: absolute;
  top: calc(100% + 6px);
  left: 0;
  right: 0;

  padding: 14px 18px;

  border-radius: 14px;

  background: rgba(9, 15, 27, 0.95);

  color: rgba(247, 249, 255, 0.7);

  font-size: 0.92rem;

  box-shadow: 0 22px 40px rgba(0, 0, 0, 0.4);

}



.trend-chart-surface {

  fill: rgba(255, 255, 255, 0.03);

  stroke: rgba(255, 255, 255, 0.06);

  stroke-width: 1;

  filter: drop-shadow(0 18px 32px rgba(0, 0, 0, 0.4));

}



.trend-axis-line {

  stroke: rgba(255, 255, 255, 0.16);

  stroke-width: 1.5;

}



.trend-axis-tick line {

  stroke: rgba(255, 255, 255, 0.24);

  stroke-width: 1;

}



.trend-axis-tick text {

  fill: rgba(247, 249, 255, 0.72);

  font-size: 0.75rem;

  font-weight: 600;

  text-anchor: middle;

}





.trend-axis-label {

  fill: rgba(247, 249, 255, 0.78);

  font-size: 0.82rem;

  font-weight: 600;

  letter-spacing: 0.04em;

}



.trend-point-highlight {

  filter: drop-shadow(0 12px 24px rgba(84, 160, 255, 0.45));

}



.trend-point-label {

  fill: rgba(247, 249, 255, 0.82);

  font-size: 0.72rem;

  font-weight: 600;

  text-anchor: middle;

}



.trend-axis-tick-rank text {

  text-anchor: end;

  transform: translateX(-8px);

}



.trend-axis-tick-year text {

  text-anchor: middle;

}













.trend-summary {

  display: flex;

  flex-wrap: wrap;

  gap: 12px 18px;

  padding: 18px 20px;

  border-radius: 18px;

  background: rgba(255, 255, 255, 0.06);

  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.05);

}



.trend-summary-pill {

  display: inline-flex;

  align-items: center;

  padding: 10px 14px;

  border-radius: 14px;

  background: rgba(9, 15, 27, 0.55);

  color: rgba(247, 249, 255, 0.88);

  font-size: 0.95rem;

  line-height: 1.45;

  box-shadow: 0 10px 18px rgba(0, 0, 0, 0.18);

}



.trend-summary-pill strong {

  color: #ffffff;

  margin-left: 4px;

}





.view-toggle {

  display: inline-flex;

  align-items: center;

  justify-content: center;

  gap: 12px;

  margin: 24px 0 18px;

}







.view-toggle-btn {

  position: relative;

  padding: 12px 28px;

  border-radius: 999px;

  border: none;

  background: linear-gradient(135deg, rgba(84, 160, 255, 0.32), rgba(140, 122, 230, 0.28));

  color: rgba(247, 249, 255, 0.88);

  font-size: 0.95rem;

  font-weight: 600;

  letter-spacing: 0.03em;

  cursor: pointer;

  transition: transform 160ms ease, color 160ms ease, background 220ms ease;

  backdrop-filter: blur(8px);

}





.view-toggle-btn::after {

  content: '';

  position: absolute;

  inset: 0;

  border-radius: inherit;

  background: radial-gradient(circle at 30% 30%, rgba(255, 255, 255, 0.4), rgba(255, 255, 255, 0));

  opacity: 0;

  transition: opacity 180ms ease;

  pointer-events: none;

}





.view-toggle-btn:hover,

.view-toggle-btn:focus {

  transform: translateY(-1px) scale(1.02);

  color: rgba(255, 255, 255, 0.96);

  outline: none;

}





.view-toggle-btn.active {

  color: #ffffff;

}





.view-toggle-btn.active::after {

  opacity: 1;

}





.view-toggle-btn:focus-visible {

  outline: 2px solid rgba(255, 255, 255, 0.7);

  outline-offset: 4px;

}







.trend-line {

  fill: none;

  stroke-linecap: round;

  stroke-linejoin: round;

